@import '../core/variables.scss';
@import '../core/mixins.scss';

.main__hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    gap: 10px;

    &--title {
        text-shadow: 0 1px 1px rgba(black, .15),
            0 3px 10px rgba(black, .15),
            0 3px 20px rgba(black, .35);
    }

    &--subtitle {
        text-align: center;

    }

    &--button {
        padding: 5px 10px;
        background: linear-gradient(90deg, $dark-lilac -1.57%, $light-lilac 98.43%);
        border-radius: 15px;
        border: none;
        cursor: pointer;
        color: $white;
        font-size: 15px;

    }
}

@media screen and (min-width:760px) {
    .main__hero {
        &--title {
            font-size: 45px;
            font-weight: bold;
        }

        &--subtitle {
            font-size: 20px;
        }

        &--button {
            font-size: 15px;
            height: 43px;
            border-radius: 20px;
            padding: 0px 15px;

        }
    }
}